import {app} from 'electron';
import * as fs from "fs-extra";
import * as path from "path";

const ROOT_DIR = path.join(app.getPath("documents"), "ninit");
const PROJECT_DIR = path.join(ROOT_DIR, "projects");

export const makeProjectDir = (prjName: string) => {
  fs.mkdirSync(path.join(PROJECT_DIR, prjName));
};

