import {
  app,
  BrowserWindow,
} from 'electron';
import * as path from 'path';
import {makeProjectDir} from "./FileManager";

// RootPath
const ROOT_PATH = "file://" + path.resolve("");

// mainWindowのHTMLファイル
const rootPath = `${ROOT_PATH}/build/react/index.html`;

// アプリ起動時の処理
app.on('ready', e => {
  makeProjectDir("testprj");
  // fs.writeFileSync( path.join(app.getPath("exe"), "test.log"), "test");
  const winSetting = { width: 1600, height: 960 };
  const mainWindow = new BrowserWindow(winSetting);
  // デベロップツールの表示
  mainWindow.webContents.openDevTools();
  mainWindow.loadURL(rootPath);
});

// アプリ終了時の処理
app.on('window-all-closed', () => {
  if (process.platform !== 'darwin') {
    app.quit();
  }
});



